import * as React from "react"
import { useState, useEffect } from "react"

function API() {
    const [request, setRequest] = useState(0)
    const [message, setMessage] = useState('loading....')
    const [count, setCount] = useState(0)


    useEffect(() => {
        try {
            let origin_url = window.location.origin
            console.log(`${origin_url}/api/items/${request}`)
            fetch(`${origin_url}/api/items/${request}`)
            .then((res) => res.json())
            .then((data) => {
                setMessage(data.item_id)
            })
            .catch((error) => {
                setMessage('an error :(')
                console.log(error)
            })
        } catch (error) {
            setMessage('an error :(')
        }
    }, [request]);

    useEffect(() => {
        console.log('button was clicked')
    }, [count]);

    return (
        <div>
            <h1>
                Press GET
            </h1>
            <button onClick={() => setRequest(request+1)}>GET</button>
            {/* <button onClick={() => setCount(count+1)}>count is: {count}</button> */}
            <small>{message}</small>
        </div>
    )
}

export default API